<template>
	<div>
		<!-- 导航栏 -->
		<!-- <div class="lf-nav" :style=" {height: htmlProportion * 120 + 'px'} ">
			<div class="lf-nav-mat" :style=" {height: htmlProportion * 40 + 'px'} "></div>
			<div class="lf-nav-text" :style=" {height: htmlProportion * 80 + 'px'} ">
				<div>
					<span class="lf-icon-close text-lg"></span>
					<span>给商户付款</span>
				</div>
				<span class="lf-icon-ellipsis text-lg"></span>
			</div>
		</div>
		<div class="lf-mat" :style="{height: htmlProportion * 120 + 'px'}"></div> -->
		<!-- 内容 -->
		<div class="lf-store-name" :style="{height: htmlProportion * 150 + 'px'}">
			<span class="text-lg" style="opacity: 0;">{{merchantName? merchantName:'测试商家名'}}</span>
		</div>
		<div class="lf-pay-panel" style="height: 10rem;">
			<div>
				<div class="one">
					<span class="text-sm">请向收银员询问应付金额</span>
				</div>
				<div class="two">
          <span class="text-llg">{{yenSymbol}}</span>
          <input class="text-lg pay-price-input" style="padding: 0.1875rem 0.3125rem;" type="number" v-model="payMoney" ref="inputRef">
				</div>
				<div class="three">
					<input v-model="remark" class="text-grey" type="text" maxlength="20" placeholder="添加备注(20个字以内)">
				</div>
			</div>
		</div>
		<div class="lf-pay-btn" :style="{height: htmlProportion * 200 + 'px'}">
			<div @click="payment">
				<span class="text-lg text-fs">付 款</span>
			</div>
		</div>
		<!-- <p>userId: {{userId}}</p> -->
		<!-- <p>进程状态: {{testProcess}}</p> -->
		<!-- <p>支付金额: {{testMoney}}</p> -->
		<!-- <p>接口反馈: {{testRes}}</p> -->
		<!-- <p>错误信息: {{testFail}}</p> -->
    <div class="zfb-mask" v-show="showMask"></div>
	</div>
</template>

<script>
	import {getWebPay, getMerchantName} from '../../../api/vueAPI.js'
	export default{
		data() {
			return{
				htmlProportion: '',		//屏幕长宽比例
				htmlWidth: '',		//屏幕宽
				htmlheight: '',		//屏幕高
				isWxPay: true,
				yenSymbol: '\u00A5',	//金钱符号
				payMoney: '',		//实付金额
				userId: '',		//商户门店userId
				testProcess: '',
				testMoney: '',
				testRes: '暂无数据',
				testFail: '暂无错误信息',
				merchantName: '测试商家',
        hasPoint: false,
        showMask: false,
        remark: '', // 备注
        equipmentId:'123'
			}
		},
    watch: {
    },
		created() {
			this.htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			this.htmlheight = document.documentElement.clientHeight || document.body.clientHeight;
			this.htmlProportion = this.htmlWidth / 750 ;
			// console.log(this.htmlWidth);
			// console.log(this.htmlheight);
			this.payMoney = sessionStorage.getItem('totalPrice')
			this.userId = sessionStorage.getItem('userId')
			let storeId = sessionStorage.getItem('storeId')
			this.storeId = '';
			if(storeId != null && storeId != '' && storeId){
				this.storeId = storeId
			}
			this.testProcess = '创建页面'
			this.testMoney = this.payMoney
			getMerchantName(this.userId).then(res => {
				this.testProcess = '商家信息调取成功'
				// this.testRes = res
				console.log(res)
				this.merchantName = res.obj
			}).catch(err => {
				console.log(err)
					this.testProcess = '商家信息调取失败，尝试获得错误信息'
					this.testFail = err
			})
		},
    mounted() {
      let eles = this.$refs.inputRef
      this.numberPercent(eles)
    },
		methods:{
		  numberPercent(eles) {
        let that = this
        eles.onkeydown = (e)=> {
          let key = e.keyCode
          if ((key > 95 && key < 106) || (key > 47 && key < 60) || key == 110 || key == 190 || key == 8 ||key == 46 || key == 13 || key == 9 || key == 37 || key == 39) {
            // 允许输入数字，小数点，删除键，回车键,8:BackSpace;9:Tab;46:Delete;37:Left;39:Right
            if (e.keyCode === 190 || e.keyCode === 110) {
              // 禁止多个点
              if (that.hasPoint) {
                return false
              } else {
                that.hasPoint = true
              }
            } else {
              // 禁止多个点
              let str = that.payMoney || ''
              if (str.indexOf('.') > -1) {
                that.hasPoint = true
              } else {
                that.hasPoint = false
              }
              if (!(e.keyCode === 8 || e.keyCode === 46)) {
                // 禁止小数点后多位
                let valArr = str.split('.')
                if (valArr[1] && valArr[1].length > 1 ) {
                  that.payMoney = parseFloat(str).toFixed(1)
                }
              }
            }
          } else {
            return false
          }
        }
      },
			/* 付款 */
			payment() {
				this.testProcess = '进行付款'
        let uuid = this.$route.query.uuid || ''
        this.showMask = true
        this.equipmentId = sessionStorage.getItem('equipmentId')
				getWebPay(this.equipmentId,2, this.payMoney, this.userId, this.storeId, uuid, this.remark).then(res => {
					this.testProcess = '付款接口调取成功，进行付款跳转'
					console.log(res)
					this.testRes = JSON.stringify(res)
					window.location.href = res.obj.payUrl		//跳转外部链接-支付宝支付链接
          setTimeout(() => {
            this.showMask = false
          }, 500)
				}).catch(err => {
					console.log(err)
          this.showMask = false
					this.testProcess = '付款接口调取失败，尝试获得错误信息'
					this.testFail = err
				})
			}
		}
	}
</script>

<style>
	body{
		background-color: #F5F2F0;
		/* font-size: 1.125rem; */
	}
	.text-sm{	/* 14 */
		font-size: 0.875rem;
	}
	.text-lg{	/* 24 */
		font-size: 1.5rem;
	}
	.text-llg{	/* 36 */
		font-size: 2.25rem;
	}
  .text-fs{
    font-size: 1.2rem;
  }
	.text-weight{
		font-weight: 800;
	}
	.text-ff4500{	/* 橙红 */
		color: #ff4500;
	}
	.text-2dc26f{	/* 绿 */
		color: #2dc26f;
	}
	.text-grey{		/* 草灰 */
		color: #a9a9a9;
	}
	.margin-right-10{
		margin-right: 0.625rem;
	}
	.lf-nav{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		/* height: 80px; */
		background-color: #F1F1F3;
		border-bottom: 0.0625rem solid #bdbdc6;
	}
	.lf-nav-mat{
		width: 100%;
		/* height: 40px; */
	}
	.lf-nav-text{
		width: 100%;
		/* height: 80px; */
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.lf-nav-text > div{
		display: flex;
		align-items: center;
	}
	.lf-nav-text span{
		margin: 0 0.625rem;
	}
	.lf-store-name{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.lf-pay-panel{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #FFF;
		border-top: 0.0625rem solid #bdbdc6;
		border-bottom: 0.0625rem solid #bdbdc6;
	}
	.lf-pay-panel > div{
		width: 80%;
		height: 9rem;
	}
	.lf-pay-panel .one {
		width: 100%;
		height: 20%;
		display: flex;
		align-items: center;
	}
	.lf-pay-panel .two {
		width: 100%;
		height: 3.5rem;
		display: flex;
		align-items: center;
		border-bottom: 0.0625rem solid #bdbdc6;
	}
	.lf-pay-panel .three {
		width: 100%;
		height: 30%;
    margin-top:2.5%;
		display: flex;
		align-items: center;
	}
	.lf-pay-panel .three input {
		border: 0;
    width: 100%;
    height: 100%;
	}
  .lf-pay-panel .three input:focus{
    outline: none;
    border: none;
  }
	.lf-pay-btn{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.lf-pay-btn > div{
		width: 80%;
		height: 50%;
		background-color: #4BA8FF;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 0.625rem;
		color: #FFF;
	}
  .pay-price-input{
    border: none;
  }
  .pay-price-input:focus{
    outline: none;
    border: none;
  }
  .zfb-mask {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 9;
  }
</style>



